window.wsft = window.wsft || {}
var funcs = {
        /*
            _url显示txt文本的路径
            callback 回调函数
        */
       getTxTStr:function(_url,callback){
        cc.loader.loadRes(_url,function(err,data){
            if(err){
                wsft.uiFuncs.showTip('获取文本失败！',30)
                callback('')
            }else{
                callback(data)
            }
        });
    },
}
wsft.funcs = module.exports = funcs;

