// Learn cc.Class:
/**
 * 加入房间
 * 功能：  push
 * author：lidongdong
 * time:2018.9.4
 */
cc.Class({
    extends: cc.Component,

    properties: {
    },

    // LIFE-CYCLE CALLBACKS:
// 不走 
    onLoad () {
        var self = this;
        this.num_lable = [];
        this.inputNum = 0;
        var join_bg = cc.find("Canvas/join_room_layer/jion_bg_1");
        this.initAllBtn();
        for(var i = 0;i < 6;i ++){
            var lable = cc.find("Canvas/join_room_layer/srk_node/join_srk_"+ i +"/join_num_label").getComponent(cc.Label);
            this.num_lable.push(lable);
        }
        var callback = function(){
            self.node.active = false;
        }
        wsft.uiFuncs.bindTouchLayer(this.node,join_bg,callback);
    },

    start () {

    },
    //active = true
    onEnable(){
        this.resetBtn()
    },
    //重置
    resetBtn(){
        cc.log("resetBtn==============")
        for(var i = 0;i < this.num_lable.length;++i){
            this.num_lable[i].string = "";
        }
        this.inputNum = 0;
    },
    //删除
    delLabelBtn(){
        if(this.inputNum > 0){
            this.inputNum  -= 1
            this.num_lable[this.inputNum].string = ""
        }
    },
    //获取房间号
    getRoomId(){
        var roomId = ""
        for(var i = 0;i <  this.num_lable.length;++i )
        {
            roomId += this.num_lable[i].string
        }
        return roomId
    },
    //输入
    inputLabel(_num){
        if(this.inputNum >= this.num_lable.length){
            return;
        }
        this.num_lable[this.inputNum].string = _num;
        this.inputNum += 1;
        if(this.inputNum == this.num_lable.length ){
            var roomId = this.getRoomId()
            this.sendRoomID(roomId)
        }
    },
    //发送服务器
    sendRoomID(roomId){
        cc.log("发送房间号-->"+roomId)
    },
    //active = false
    onDisable(){
        
    },
    
    allBtnCallback:function(event,name){
        cc.log(name)
        if(name === "close"){
            this.node.active = false
        }
        else if(name == 10){
            this.resetBtn()
        }
        else if(name == 11){
            this.delLabelBtn()
        }
        else{
            this.inputLabel(name)
        }

    },
   // 初始化所有的btn
   initAllBtn:function(){
        cc.log("btn==============")
        var self = this
        var btnHader = function(btnPath,name){
            var btn = cc.find(btnPath)
            wsft.event.addBtnEvent(btn,self.node,'joinLayer','allBtnCallback',name);
        }
        btnHader('Canvas/join_room_layer/close_btn','close'); //close
        for (var i = 0;i<12;++i){
            btnHader('Canvas/join_room_layer/btn_node/num_'+ i,i); //close
        }
       
   },
    // update (dt) {},
});
