/**
 * 跑马灯
 * 功能：  1默认的公告 [在没有广播的情况下.这个就循环滚动]
 *        2广播只播放一次
 * author：lidongdong
 * time:2018.8.31
 */

cc.Class({
    extends: cc.Component,

    properties: {
        defaultNotice:null, //默认的公告 [在没有广播的情况下.这个就循环滚动]
        isRuning:false,
        noticeTable:[cc.String],//广播
        speed:100,//速度
    },

    // LIFE-CYCLE CALLBACKS:

    onLoad () {
        this.Lable = cc.find('Canvas/notice_node/mask_node/notice_label')
        this._lable = this.Lable.getComponent(cc.Label)
        this.mask = cc.find('Canvas/notice_node/mask_node')
    },
    
    start () {
        
    },
    //设置速度
    setSpeed(_speed){
        this.speed = _speed
    },
    //设置默认公告
    setDefaultNotice(_defaultNotice){
        if ( _defaultNotice == undefined){return}
        cc.log("设置默认公告==="+ _defaultNotice);
        this.defaultNotice = _defaultNotice
        this.runNotice();

    },
    //添加广播
    addNoticeTable(_noticeTable){
        if( _noticeTable == undefined){return}
        cc.log("添加广播==="+ _noticeTable);
        this.noticeTable.push(_noticeTable);
        this.runNotice();
    },
    runNotice(){
        var self = this
        if (this.isRuning) {
            return
        }
        var str = ""
        if( this.noticeTable.length == 0){
            if (this.defaultNotice) {
                str = this.defaultNotice
            } else {
                self.node.active = false
                return
            }
        }else{
            // str = this.noticeTable[0]
            str = this.noticeTable.shift()
        }
        this.isRuning = true
        this._lable.string = str
        this.Lable.x = this.mask.width / 2
        this.Lable.y = 0
        //下一帧的处理
        var ac = cc.sequence(
            cc.moveBy(0, 0, 0),
            cc.callFunc(function(){
                self._runAction();
            })
        )
        self.Lable.runAction(ac)
    },
    _runAction(){
        var self = this
        var moveX = this.mask.width + this.Lable.width
        var time = moveX/this.speed
        var ac = cc.sequence(
            cc.moveBy(time, -moveX, 0),
            cc.callFunc(function(){
                self.isRuning = false;
                self.runNotice();
            })
        )
        self.Lable.runAction(ac)
    },   
});
